<template>
  <!--  -->
  <div class="wrapper" ref="wrapper">
    <div class="content">
      <slot>
        <ul>
          <li>1</li>
          <li>2</li>
          <li>3</li>
          <li>4</li>
          <li>5</li>
          <li>6</li>
          <li>7</li>
          <li>8</li>
          <li>9</li>
          <li>10</li>
          <li>10</li>
          <li>10</li>
          <li>10</li>
          <li>10</li>
          <li>10</li>
          <li>10</li>
          <li>10</li>
          <li>10</li>
          <li>10</li>
          <li>10</li>
          <li>10</li>
          <li>10</li>
          <li>10</li>
          <li>10</li>
          <li>10</li>
          <li>10</li>
        </ul>
      </slot>
    </div>
  </div>
</template>

<script>
import BScroll from "better-scroll";
export default {
  name: "scroll",
  data() {
    return {
      scroll: null,
    };
  },
  props: {
    //scroll
    probeType: {
      type: Number,
      default: 0,
    },
    //
    duration: {
      type: Number,
      default: 300,
    },
    //
    pullUpLoad: {
      type: Boolean,
      default: false,
    },
    //
    loadTime: {
      tyoe: Number,
      default: 500,
    },
  },
  mounted() {
    //scroll20ms,bs.refresh()dom
    //
    // setTimeout(() => {
    //   this.scroll = new BScroll(this.$refs.wrapper, {
    //     click: true,
    //   });
    //   this.$emit("refresh", this.scroll);
    // }, 1000);
    //betterscroll,dom,refresh()
    this.$nextTick(() => {
      this.scroll = new BScroll(this.$refs.wrapper, {
        click: true, //  tabCtrl
        probeType: this.probeType, //  
        pullUpLoad: this.pullUpLoad, //
      });
      //refscroll
      // this.$emit("refresh", this.scroll);
      this.scroll.on("scroll", (pos) => {
        this.$emit("scroll", pos);
      });
      if (this.pullUpLoad) {
        this.scroll.on("pullingUp", () => {
          this.$emit("pullingUp");
          setTimeout(() => {
            this.scroll.finishPullUp();
          }, this.loadTime);
        });
      }
    });
  },
  methods: {
    refresh() {
      // console.log("refresh");
      this.scroll.refresh();
      return "success";
    },
    scrollTo(x, y, time = this.duration) {
      this.scroll.scrollTo(x, y, time);
    },
    getY() {
      return this.scroll.y;
    },
  },
};
</script>

